@font-face {
  font-family: 'Roboto';
  src: url('fonts/Roboto-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

body {
  background-color: #e1e388;
}

.field {
  	display: -ms-flexbox;
  	display: flex;
  	padding: 10px;
  	width: 100%;
  	margin: 0 auto;
  	color: #fff;
  	font-size: 1.3em;
  	-ms-flex-direction: column;
  	    flex-direction: column;
  	-ms-flex-align: center;
  	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
}

.box {
  border: 2px solid #aaa719;
  background-color: #d4d37f;
  margin-bottom: 20px;
  padding: 10px;
  width: 260px;
}

.box:last-child {
  margin-bottom: 0;
}

button.btn {
  margin-right: 10px;
  padding: 10px;
  margin-top: 10px;
  cursor: pointer;
  font-size: 0.65em;
  color: #fff;
  outline: none;
}

.box {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
}

.box.btnBox{
	display: -ms-flexbox;
	display: flex;
}

.box .text{
	font-family: 'Roboto';
	color: #000;
	text-transform: uppercase;
	font-weight: 600;
}

.box p{
	font-family: 'Roboto';
	color: #000;
}


.box input {
	margin: 20px;
	padding: 10px 0;
	padding-left: 15px;
	border-radius: 3px;
	outline: none;
	text-decoration: none;
	-webkit-box-shadow: none;
	        box-shadow: none;
	border: none;
}

.box textarea {
	margin: 20px;
	margin-top: 0;
	padding-left: 15px;
	border-radius: 5px;
	outline: none;
	text-decoration: none;
	-webkit-box-shadow: none;
	        box-shadow: none;
	border: none;
}

.btn.light {
  background-color: #4eafc9;
  border: 2px solid #2a89a2;
}

.btn.red {
  background-color: #dda66d;
  border: 2px solid #b6821a;
}

.btn.new {
	background-color: #6BE400;
	border: 2px solid #469400;
	margin-bottom: 25px;
}

.btn.success{
	background-color: #FF7400;
	border: 2px solid #A64B00;
}

.btn.success:hover {
	background-color: #FF5300;
  	border-color: #A63600;
}

.btn.new:hover {
	background-color: #269926;
  	border-color: #008500;
}

.btn.light:hover {
  background-color: #2a89a2;
  border-color: #1d6f85;
}

.btn.red:hover {
  background-color: #b6821a;
  border-color: #946912;
}


